import React, { useState } from "react";
import personService from "../services/persons";
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import "./Persons.css";
import Error from "./Error";

const Persons = ({ persons }) => {
  const [errorMessage, setErrorMessage] = useState(null);

  return (
    <div className = "container">
      <h2>phonebook ðŸ“žðŸ“–</h2>
      <Error message={errorMessage} />
      <ul>
        {persons.map((person) => (
          <li key={person.name}>
            {person.name}: {person.number}{" "}
            <IconButton
              onClick={() => {
                if (window.confirm(`Delete ${person.name} from the phonebook?`))
                  //TODO, update state somehow
                  personService
                    .remove(person.id)
                    .then(() => {
                      setErrorMessage(
                        `${person.name} has successfully been removed from the phonebook. Please refesh the page to see deletion.`
                      );
                      setTimeout(() => setErrorMessage(null), 5000);
                    })
                    .catch((error) => {
                      setErrorMessage(
                        `${person.name} has already been removed. Please refresh the page to see deletion.`
                      );
                      setTimeout(() => setErrorMessage(null), 5000);
                    });
              }}
            >
              <DeleteIcon/>
            </IconButton>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Persons;
