import React, { useState, useEffect } from "react";
import Filter from "./components/Filter";
import PersonForm from "./components/PersonForm";
import Persons from "./components/Persons";
import personService from "./services/persons";
import Error from "./components/Error";
import "./App.css";

const Success = ({ message }) => {
  if (message === null) {
    return null;
  }

  return <div className="success">{message}</div>;
};

//@TODO update state on http delete in Persons.js

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [filter, setFilter] = useState("");
  const [successMessage, setSuccessMessage] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    personService.getAll().then((peopleList) => {
      setPersons(peopleList);
    });
  }, []);

  const handleNameChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value);
  };

  const handleFilterChange = (event) => {
    setFilter(event.target.value);
  };

  const formatPhoneNumber = (phoneNumber) => {
    return (
      "(" +
      phoneNumber.substring(0, 3) +
      ") " +
      phoneNumber.substring(3, 6) +
      "-" +
      phoneNumber.substring(6, 11)
    );
  };

  const handleNameSubmit = (event) => {
    event.preventDefault();
    const personObject = {
      name: newName,
      number: formatPhoneNumber(newNumber),
    };

    const phoneBookContains = persons.filter(
      (person) =>
        newName.length !== 0 &&
        newName.includes(" ") &&
        person.name.toLowerCase().includes(newName.toLowerCase())
    );
    const existingPerson = phoneBookContains[0];

    const updatedExistingPerson = {
      ...existingPerson,
      number: formatPhoneNumber(newNumber),
    };
    if (existingPerson !== undefined) {
      if (
        window.confirm(
          `${existingPerson.name} is already in the phonebook. Replace their current phone number with a new one?`
        )
      ) {
        if (newNumber.length === 10) {
          personService
            .update(existingPerson.id, updatedExistingPerson)
            .then(
              () => {
                setPersons(
                  setPersons.map((person) =>
                    person.id !== existingPerson.id //create new array mapping non-matching ID's to original persons, if id matches, then map to our updatedExistingPerson
                      ? person
                      : updatedExistingPerson
                  )
                );
              }, //@TODO, trigger rerender of people list on replacement
              setSuccessMessage(
                `${existingPerson.name}'s phone number has successfully been replaced.`
              ),
              setTimeout(() => {
                setSuccessMessage(null);
              }, 5000),
              setNewNumber(""),
              setNewName("")
            )
            .catch((error) => {
              console.log(error);
            });
        } else {
          setErrorMessage(
            `Phone number must contain ${10 - newNumber.length} more digits.`
          );
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        }
      }
    } else if (
      newName.includes(" ") &&
      newName.length > 0 &&
      newNumber.length === 10
    ) {
      personService.create(personObject).then(
        setSuccessMessage(
          `${personObject.name} has successfully been added to the phonebook.`
        ),
        setTimeout(() => {
          setSuccessMessage(null);
        }, 5000)
      );
      setPersons(persons.concat(personObject));
      setNewName("");
      setNewNumber("");
    } else if (
      !newName.includes(" ") ||
      newNumber.length < 10 ||
      newNumber.length > 10
    ) {
      let errorMessage = "";
      if (!newName.includes(" ")) {
        errorMessage += "Name must contain a first and last name.";
      }
      if (newNumber.length < 10 || newNumber.length > 10) {
        errorMessage += ` Phone number must contain ${
          10 - newNumber.length
        } more digits.`;
      }
      setErrorMessage(errorMessage);
      setTimeout(() => {
        setErrorMessage(null);
      }, 5000);
    }
  };

  const filteredPersons = persons.filter((person) =>
    person.name.toLowerCase().includes(filter.toLowerCase())
  );

  return (
    <div className = "big-container">
      <h1 className = "header">phonebook</h1>
      <div className = "messages">
        <Success className="success" message={successMessage} />
        <Error className="error" message={errorMessage} />
      </div>
      <Filter value={filter} onChange={handleFilterChange} />
      <PersonForm
        newName={newName}
        newNumber={newNumber}
        handleNameSubmit={handleNameSubmit}
        handleNumberChange={handleNumberChange}
        handleNameChange={handleNameChange}
      />
      <Persons persons={filteredPersons} />
    </div>
  );
};

export default App;
